<template>
    <div>
        <v-dialog
            max-width="400"
            persistent
            v-model="aksessuarAddDialog"
        >
            <template>
                <v-card class="aksessuar_add">

                    <div class="aksessuar_add__title">
                        <span class="text-black">Aksessuar qoshilmoqda</span>
                        <v-btn icon @click="closeDialog">
                            <img src="@/assets/images/BackButton.svg" alt="BackButton">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formAksessuarAdd" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit3)">

                            <div class="mt-3">
                                <span class="text-black ml-1">Sana</span>

                                <v-menu
                                    v-model="dateMenu"
                                    :close-on-content-click="false"
                                    :nudge-right="40"
                                    transition="scale-transition"
                                    offset-y
                                    min-width="auto"
                                >
                                    <template v-slot:activator="{ on, attrs }">
                                        <ValidationProvider rules="required" v-slot="{ errors }">
                                            <v-text-field
                                                class="mt-2"
                                                placeholder="Tanlang"
                                                v-model="dateFormatted"
                                                readonly
                                                outlined
                                                v-bind="attrs"
                                                v-on="on"
                                                @blur="date = parseDate(dateFormatted)"
                                                :error-messages="errors"
                                            ></v-text-field>
                                        </ValidationProvider>
                                    </template>
                                    <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-date-picker
                                        color="blue lighten-1"
                                        v-model="date"
                                        @input="dateMenu = false"
                                        locale="ru-ru"
                                        first-day-of-week="1"
                                        :error-messages="errors"
                                    ></v-date-picker>
                                    </ValidationProvider>
                                </v-menu>
                            </div>

                            <div>
                                <span class="text-black ml-1">Qayerdan olindi</span>
                                <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'from_where']}" v-slot="{ errors }">
                                    <v-text-field  outlined class="mt-2" :error-messages="errors"  maxlength="100"
                                        placeholder="Misol: Xitoydan" v-model="new_aksessuar.from_where">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>

                            <div>
                                <span class="text-black ml-1">Aksessuar turini tanlang</span>
                                <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-text-field append-icon="mdi-chevron-right"
                                        @click="aksessuarSelectDialog = true"
                                        label="Tanlang" outlined class="mt-2"
                                        :value="existingAksessuarType.name"
                                        :error-messages="errors">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>

                            <div>
                                <span class="text-black ml-1">Mahsulotni tanlang</span>
                                <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-text-field append-icon="mdi-chevron-right"
                                        @click="productSelectDialog = true"
                                        label="Tanlang" outlined class="mt-2"
                                        :value="existingProductType.name"
                                        :error-messages="errors" :disabled="!existingAksessuarType">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>

                            <div>
                                <span class="text-black ml-1">Adadini kiriting</span>
                                <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'amount']}" v-slot="{ errors }">
                                    <v-text-field :error-messages="errors"
                                        @input="formatPrice(new_aksessuar.amount, 'amount')"
                                        outlined class="mt-2"
                                        v-model="new_aksessuar.amount"
                                        placeholder="Misol: 1,200 kg">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>

                            <div>
                                <span class="text-black ml-1">Dona narxi</span>
                                <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'price']}" v-slot="{ errors }">
                                    <v-text-field :error-messages="errors"
                                        @input="formatPrice(new_aksessuar.price, 'price')"
                                        v-model="new_aksessuar.price"
                                        outlined class="mt-2"
                                        placeholder="Misol: 240,000 som">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>

                            <div>
                                <span class="text-black ml-1">Umumiy summa</span>
                                <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'total_price']}" v-slot="{ errors }">
                                    <v-text-field
                                        @input="formatPrice(new_aksessuar.total_price, 'total_price')"
                                        outlined :error-messages="errors"
                                        :value="getTotalPrice"
                                        v-model="new_aksessuar.total_price" class="mt-2"
                                        placeholder="Misol: 1,240,000 som">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>

                            <div>
                                <span class="text-black ml-1">Berilgan summa</span>
                                <ValidationProvider :rules="{error: [errorStatus, errorMessage, errorPlace, 'paid']}"
                                v-slot="{ errors }">
                                    <v-text-field :error-messages="errors"
                                        @input="formatPrice(new_aksessuar.paid, 'paid')"
                                        outlined min="1"
                                        placeholder="Misol: 1,000,000 som"
                                        class="mt-2" v-model="new_aksessuar.paid">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                    @click="closeDialog"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>

                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>


        <v-dialog
            max-width="400"
            persistent
            v-model="aksessuarSelectDialog"
            style="z-index: 999;"
        >
            <template>
                <v-card class="aksessuar_turi_add">

                    <div class="aksessuar_turi_add__title">
                        <span>Aksessuar turini tanlang</span>
                        <v-btn icon @click="aksessuarSelectDialog = false">
                            <img src="@/assets/images/BackButton.svg" alt="BackButton">
                        </v-btn>
                    </div>

                     <ValidationObserver ref="formAksessuarSelect" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_turi_add__content" @submit.prevent="handleSubmit(onSubmit1)">

                            <div class="exists mt-5">
                                <span class="exists__title text-black">Mavjud aksessuarlar</span>
                                <v-text-field v-model="aksessuarSelect"
                                    outlined class="exists__input" >
                                    <template v-slot:prepend-inner>
                                        <img src="@/assets/icons/search-normal.svg"
                                        class="mb-4 mt-0" alt="">
                                    </template>
                                </v-text-field>
                            </div>

                            <perfect-scrollbar class="mr-1">
                                <div class="radioContent mb-5" v-for="(element, index)
                                 in filteredAksessuarTypes" :key="index">
                                    <span>{{ element.name }}</span>
                                    <ValidationProvider :rules="newAksessuarType == '' ? 'required' : ''" v-slot="{ errors }">
                                        <v-radio-group class="radioGroup" :error-messages="errors"
                                            v-model="existingAksessuarType">
                                            <v-radio :value="element"></v-radio>
                                        </v-radio-group>
                                    </ValidationProvider>
                                </div>
                            </perfect-scrollbar>

                            <div class="mt-4">
                                <span class="text-black ml-1">Yangi aksessuar turi</span>
                                <ValidationProvider :rules="existingAksessuarType == '' ? 'required' : ''" v-slot="{ errors }">
                                    <v-text-field outlined class="mt-2" :error-messages="errors"
                                        v-model="newAksessuarType" placeholder="Nomni kiriting" maxlength="100">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                    @click="aksessuarSelectDialog = false"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>

                        </form>
                    </ValidationObserver>

                </v-card>
            </template>
        </v-dialog>


        <v-dialog
            max-width="400"
            persistent
            v-model="productSelectDialog"
            style="z-index: 999;"
        >
            <template>

                <v-card class="aksessuar_turi_add">
                    <div class="aksessuar_turi_add__title" id="ffas">
                        <span>Mahsulotni tanlang</span>
                        <v-btn icon @click="productSelectDialog = false">
                            <img src="@/assets/images/BackButton.svg" alt="">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formProductSelect" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_turi_add__content" @submit.prevent="handleSubmit(onSubmit2)">

                            <div class="exists mt-5">
                                <span class="exists__title text-black">Mavjud mahsulotlar</span>
                                <v-text-field v-model="productSelect" outlined
                                    class="exists__input">
                                    <template v-slot:prepend-inner>
                                        <img src="@/assets/icons/search-normal.svg"
                                        class="mb-4 mt-0" alt="">
                                    </template>
                                </v-text-field>
                            </div>

                            <perfect-scrollbar class="mr-1" @ps-y-reach-end="addItems">
                                <div class="radioContent mb-5" v-for="(element, index2) in filteredProducts" :key="index2">
                                    <span>{{ element.name }}</span>
                                    <ValidationProvider
                                    :rules="newProduct.name == '' && newProduct.code == '' ? 'required' : ''"
                                    v-slot="{ errors }">
                                        <v-radio-group class="radioGroup" :error-messages="errors"
                                            v-model="existingProductType">
                                            <v-radio :value="element"></v-radio>
                                        </v-radio-group>
                                    </ValidationProvider>
                                </div>
                            </perfect-scrollbar>

                            <div class="mt-4">
                                <span class="text-black ml-1">Yangi mahsulot</span>
                                <ValidationProvider :rules="existingProductType == '' ? 'required' : ''"
                                 v-slot="{ errors }">
                                    <v-text-field  outlined class="mt-4" :error-messages="errors"
                                        v-model="newProduct.name" placeholder="Nomni kiriting" maxlength="100">
                                    </v-text-field>
                                </ValidationProvider>

                                <ValidationProvider :rules="existingProductType == '' ? 'required' : ''"
                                 v-slot="{ errors }">
                                    <v-text-field  outlined :error-messages="errors" type="number"
                                        v-model="newProduct.code" placeholder="Kodni kiriting">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                    @click="productSelectDialog = false"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>
                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>
    </div>
</template>

<script>
import {mapActions, mapState} from "vuex";
import {PerfectScrollbar} from 'vue2-perfect-scrollbar'
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'

export default {
    props: ['aksessuarAddDialog'],
    mixins: [dateMixin, loaderMixin],
    components: {
        PerfectScrollbar
    },
    data() {
        return {
            // ============= SECONDARY_DATA =============
            page: 1,
            last_page: 2,
            pageCount: 0,
            new_aksessuar: {
                from_where: '',
                product_id: '',
                amount: 0,
                price: 0,
                total_price: '',
                paid: 0
            },
            aksessuarSelect: '',
            productSelect:'',

            existingAksessuarType: '',
            existingProductType: '',
            productStatus: '',

            newAksessuarType: '',
            newProduct: {
                name: '',
                code: ''
            },

            filteredAksessuarTypes: '',
            filteredProducts: '',

            aksessuarSelectDialog: false,
            productSelectDialog: false,
            // ============= SECONDARY_DATA =============




            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    async mounted() {
        await this.refresh()
        await this.refresh2()
    },
    watch: {



        // ============= OTHER_WATCHERS START =============
        async aksessuarSelect(newValue, oldValue) {
            clearTimeout(this._timerId)

            this._timerId = setTimeout(async () => {
                if(newValue){
                    await this.fetchCategories({search: newValue})
                }else if(newValue == ''){
                    await this.fetchCategories()
                }
            }, 500)
        },
        async productSelect(newValue, oldValue){
            clearTimeout(this._timerId)

            this._timerId = setTimeout(async () => {
                if(newValue){
                    await this.fetchProductCodes({status: this.productStatus, search: newValue})
                }else if(newValue == ''){
                    await this.fetchProductCodes({status: this.productStatus})
                }
            }, 500)
        },





        newAksessuarType(newValue, oldValue){
            if(newValue != ''){
                this.existingAksessuarType = ''
            }
        },

        async existingAksessuarType(newValue, oldValue){
            if(newValue != ''){
                this.newAksessuarType = ''

                this.productStatus = newValue.id
                await this.refresh()
            }
        },

        'newProduct.name': {
            handler(newValue){
                if(newValue != ''){
                    this.existingProductType = ''
                }
            },deep: true
        },

        'newProduct.code': {
            handler(newValue){
                if(newValue != ''){
                    this.existingProductType = ''
                }
            },deep: true
        },

        existingProductType(newValue, oldValue){
            if(newValue != ''){
                this.newProduct.name = ''
                this.newProduct.code = ''
            }
        }
        // ============= OTHER_WATCHERS END =============
    },
    computed: {
        ...mapState({
            productCategories: state => state.categories.productCategories,
            productCodesFields: state => state.code.productCodesFields,
        }),
        // ============= CALCULATE_TOTAL_PRICE START =============
        getTotalPrice() {
            let price = this.new_aksessuar.price.toString().replace(/,/g, '')
            let amount = this.new_aksessuar.amount.toString().replace(/,/g, '');

            let result = price * amount

            return  this.new_aksessuar.total_price = new Intl.NumberFormat('en-US').format(result)
        }
        // ============= CALCULATE_TOTAL_PRICE END =============
    },
    methods: {
        ...mapActions(
            {
                saveProductCode: 'code/saveProductCode',
                fetchCategories: 'categories/fetchCategories',
                fetchProductCodes: 'code/fetchProductCodes',
                postCategories: 'categories/postCategories',
                postNewIp: 'ipAndAksessuar/postNewIp'
            }
        ),
        async addItems(){
            if(this.page < this.last_page){
                this.page = this.page + 1;

                clearTimeout(this._timerId)

                this._timerId = setTimeout(async () => {
                    await this.fetchProductCodes({page: this.page, status: 1, search: this.ipSelect})
                   
                    this.page = this.productCodesFields.current_page;
                    this.pageCount = Math.ceil(this.productCodesFields.total / 20);

                    this.existingIps = this.existingIps.concat(this.productCodesFields.data);
                }, 500)
            }
        },



       // ============= REFRESH START =============
        async refresh(){
            await this.fetchProductCodes({status: this.productStatus})
            this.filteredProducts = this.productCodesFields.data
            // .filter(el => el.product_category_id == 4)
        },

        async refresh2(){
            await this.fetchCategories()
            this.filteredAksessuarTypes = this.productCategories.filter(el => el.parent_id == 4)
        },
        // ============= REFRESH END =============









        // ============= PRICE_FORMATTER START =============
        formatPrice(value, input) {
            if(value != ''){

                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);


                if(input == 'amount'){
                    this.new_aksessuar.amount = currency
                }else if(input == 'price'){
                    this.new_aksessuar.price = currency
                }else if(input == 'total_price'){
                    this.new_aksessuar.total_price = currency
                }else if(input == 'paid'){
                    this.new_aksessuar.paid = currency
                }
            }
        },
        // ============= PRICE_FORMATTER END =============








        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.new_aksessuar = {from_where: '', product_id: '', amount: 0,
                price: 0, total_price: '', paid: 0}
            this.new_product = {name: '', code: ''}

            this.existingAksessuarType = ''
            this.existingProductType = ''

            this.aksessuarSelect = ''
            this.productSelect = ''

            this.productStatus = ''
            this.newAksessuarType = ''

            this.date = ''
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeDialog(){
            this.clearInputs();
            this.$parent.aksessuarAddDialog = false
            this.$refs.formAksessuarAdd.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============









        // ============= ON_SUBMIT1 START =============
        async onSubmit1(){
            this.$refs.formAksessuarSelect.validate().then(async(success) => {
                if(success){
                    if(this.newAksessuarType != ''){
                        let resp = await this.postCategories({name: this.newAksessuarType, parent_id: 4});
                        await this.refresh2()
                        this.existingAksessuarType = resp[1]
                        this.$toast.success('Muvaffaqiyatli qo`shildi!');
                        this.aksessuarSelectDialog = false
                        this.$store.commit('main/SET_UPDATE_MAVJUD_IPS', true)
                    }else{
                        this.aksessuarSelectDialog = false
                    }
                }
            })
        },
        // ============= ON_SUBMIT1 END =============











        // ============= ON_SUBMIT2 START =============
        async onSubmit2(){
            this.$refs.formProductSelect.validate().then(async(success) => {
                if(success){
                    if(this.newProduct.name != '' && this.newProduct.code != ''){
                        let resp = await this.saveProductCode({name: this.newProduct.name,
                            code: this.newProduct.code, product_category_id: this.productStatus});
                        await this.refresh()
                        this.existingProductType = resp
                        this.$toast.success('Muvaffaqiyatli qo`shildi!');
                        this.productSelectDialog = false
                    }else{
                        this.productSelectDialog = false
                    }
                }
            })
        },
        // ============= ON_SUBMIT2 END =============











        // ============= ON_SUBMIT3 START =============
        async onSubmit3(){
            this.$refs.formAksessuarAdd.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============



                    // ============= ADDING_NEW_IP START =============
                    let aksessuar = await this.postNewIp(
                        {
                            created_at: this.date,
                            from_where: this.new_aksessuar.from_where,
                            product_id: parseInt(this.existingProductType.id),
                            amount: parseFloat(this.new_aksessuar.amount.toString().replace(/,/g, '')),
                            price: parseFloat(this.new_aksessuar.price.toString().replace(/,/g, '')),
                            total_price: parseFloat(this.new_aksessuar.total_price.toString().replace(/,/g, '')),
                            paid: parseFloat(this.new_aksessuar.paid.toString().replace(/,/g, '')),
                            type: 'accessory'
                        })

                    if(aksessuar.response && aksessuar.response.status == 422){
                        if(aksessuar.response.data.errors.from_where){
                            this.errorMessage = aksessuar.response.data.errors.from_where[0];
                            this.errorPlace = 'from_where'
                        }else if(aksessuar.response.data.errors.amount){
                            this.errorMessage = aksessuar.response.data.errors.amount[0];
                            this.errorPlace = 'amount'
                        }else if(aksessuar.response.data.errors.price){
                            this.errorMessage = aksessuar.response.data.errors.price[0];
                            this.errorPlace = 'price'
                        }else if(aksessuar.response.data.errors.total_price){
                            this.errorMessage = aksessuar.response.data.errors.total_price[0];
                            this.errorPlace = 'total_price'
                        }else if(aksessuar.response.data.errors.paid){
                            this.errorMessage = aksessuar.response.data.errors.paid[0];
                            this.errorPlace = 'paid'
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else if(aksessuar.response && aksessuar.response.status == 409){
                        if(aksessuar.response.data.message){
                            this.errorMessage = aksessuar.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        let toastTitle = 'Muvaffaqiyatli qo`shildi!'
                        this.$toast.success(toastTitle);
                        this.closeDialog();
                        this.$store.commit('main/SET_UPDATE_SUPPLY_ACCESSORY_INCOME', true)
                        this.$store.commit('main/SET_UPDATE_SUPPLY_ACCESSORY_EXISTS', true)
                    }
                    // ============= ADDING_NEW_IP END =============





                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        }
        // ============= ON_SUBMIT3 END =============

    },


}
</script>


<style>

</style>

