<template>
   <v-dialog
        max-width="400"
        persistent
        v-model="ipAddDialog"
    >
        <template>
            <v-card class="ip_add">

                <div class="ip_add__title">
                    <span class="text-black">Ip qoshilmoqda</span>
                    <v-btn icon @click="closeIpAddDialog">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>

                <ValidationObserver ref="formIpAdd" v-slot="{ handleSubmit, invalid }">

                    <form class="ip_add__content" @submit.prevent="handleSubmit(onSubmit)">
                
                        <div class="mt-3">
                            <span class="text-black ml-1">Sana</span>
                            
                                <v-menu
                                    v-model="dateMenu"
                                    :close-on-content-click="false"
                                    :nudge-right="40"
                                    transition="scale-transition"
                                    offset-y
                                    min-width="auto"
                                >
                                    <template v-slot:activator="{ on, attrs }">
                                        <ValidationProvider rules="required" v-slot="{ errors }">
                                            <v-text-field
                                                class="mt-2"
                                                placeholder="Tanlang"
                                                v-model="dateFormatted"
                                                readonly
                                                outlined
                                                v-bind="attrs"
                                                v-on="on"
                                                @blur="date = parseDate(dateFormatted)"
                                                :error-messages="errors"
                                            ></v-text-field>
                                        </ValidationProvider>
                                    </template>
                                    <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-date-picker
                                        color="blue lighten-1"
                                        v-model="date"
                                        @input="dateMenu = false"
                                        locale="ru-ru"
                                        first-day-of-week="1"
                                        :error-messages="errors"
                                    ></v-date-picker>
                                    </ValidationProvider>
                                </v-menu>
                        </div>

                        <div>
                            <span class="text-black ml-1">Partiya raqami</span>
                            <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'from_where']}" v-slot="{ errors }">
                                <v-text-field class="mt-2" outlined  maxlength="100"
                                    v-model="new_ip.from_where" :error-messages="errors"
                                    placeholder="Misol: 182455">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div class="part_content">

                            <span class="text-black ml-1">Import turini tanlang</span>
                            <v-list class="d-flex">
                                <div class="radioContent">
                                    <span>Mavjud ip</span>
                                    <v-radio-group class="radioGroup" v-model="ip_type">
                                        <v-radio value="existing"></v-radio>
                                    </v-radio-group>
                                </div>
                                <div class="radioContent">
                                    <span>Yangi ip</span>
                                    <v-radio-group class="radioGroup" v-model="ip_type">
                                        <v-radio value="new"></v-radio>
                                    </v-radio-group>
                                </div>
                            </v-list>


                            <div v-if="ip_type == 'new'" class="mt-4">
                                <span class="text-black ml-1">Nom va kod</span>
                                <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'name']}" v-slot="{ errors }">
                                    <v-text-field 
                                        v-model="new_product.name" class="mt-2" outlined maxlength="100"
                                        placeholder="Nomni kiriting" :error-messages="errors">
                                    </v-text-field>
                                </ValidationProvider>

                                <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'code']}" v-slot="{ errors }">
                                    <v-text-field
                                        type="number" :error-messages="errors"
                                        v-model="new_product.code" outlined placeholder="Kodni kiriting">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>
                            
                            
                            <div v-else-if="ip_type == 'existing'">
                                <div class="exists">
                                    <span class="exists__title text-black">Mavjud ipni tanlang</span>
                                    <v-text-field 
                                        v-model="ipSelect" outlined class="exists__input">
                                        <template v-slot:prepend-inner>
                                            <img src="@/assets/icons/search-normal.svg" 
                                            class="mb-4 mt-0" alt="search-normal">
                                        </template>
                                    </v-text-field>
                                </div>

                                <perfect-scrollbar class="mr-1" @ps-scroll-up="scrolledToTop" @ps-y-reach-end="addItems">

                                    <div class="radioContent mb-5" v-for="(element, index) in existingIps" :key="index">
                                        <span>#{{ element.code }} | {{ element.name }}</span>
                                        <ValidationProvider rules="required" v-slot="{ errors }">
                                            <v-radio-group class="radioGroup" v-model="existingIp" :error-messages="errors">
                                                <v-radio :value="element.id"></v-radio>
                                            </v-radio-group>
                                         </ValidationProvider>
                                    </div>
                                </perfect-scrollbar>
                            </div>


                            <div :class="ip_type == 'existing' ? 'mt-6' : ''">
                                <span class="text-black ml-1">Massa (kg)</span>
                                <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'amount']}" v-slot="{ errors }">
                                    <v-text-field :error-messages="errors"
                                        @input="formatPrice(new_ip.amount, 'amount')"
                                        v-model="new_ip.amount" min="1"
                                        outlined placeholder="Misol: 1,200 kg" class="mt-2">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <div>
                                <span class="text-black ml-1">KG summa</span>
                                <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'price']}" v-slot="{ errors }">
                                    <v-text-field :error-messages="errors"
                                        @input="formatPrice(new_ip.price, 'price')"
                                        v-model="new_ip.price" min="1"
                                        outlined placeholder="Misol: 240,000 som" class="mt-2">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <div>
                                <span class="text-black ml-1">Umumiy summa</span>
                                <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'total_price']}" v-slot="{ errors }">
                                    <v-text-field :error-messages="errors"
                                        @input="formatPrice(new_ip.total_price, 'total_price')"
                                        :value="getTotalPrice" outlined min="1"
                                        placeholder="Misol: 1,240,000 som"
                                        class="mt-2" v-model="new_ip.total_price">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <div>
                                <span class="text-black ml-1">Berilgan summa</span>
                                <ValidationProvider :rules="{error: [errorStatus, errorMessage, errorPlace, 'paid']}" v-slot="{ errors }">
                                    <v-text-field :error-messages="errors"
                                        @input="formatPrice(new_ip.paid, 'paid')"
                                        outlined min="1"
                                        placeholder="Misol: 1,000,000 som"
                                        class="mt-2" v-model="new_ip.paid">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>

                        </div>
                    
                        <v-card-actions class="d-flex justify-space-between">
                            <v-btn
                                class="backBtn btn"
                                elevation="0"
                                @click="closeIpAddDialog"
                            >Bekor qilish
                            </v-btn>
                            <v-btn
                                class="primaryBtn btn"
                                elevation="0"
                                :loading="loading"
                                :disabled="invalid"
                                type="submit"
                                @click="loader = 'loading'"
                            >Tasdiqlash
                            </v-btn>
                        </v-card-actions>

                    </form>
                </ValidationObserver>
            </v-card>
        </template>
    </v-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { PerfectScrollbar } from 'vue2-perfect-scrollbar'
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'

export default {
    props: ['ipAddDialog'],
    mixins: [dateMixin, loaderMixin],
    components: {
        PerfectScrollbar
    },
    data(){
        return{
            // ============= SECONDARY_DATA =============
            page: 1,
            last_page: 2,
            pageCount: 0,
            ipSelect: '',
            ip_type: 'new',
            new_ip:{
                from_where: '',
                product_id: '',
                amount: 0,
                price: 0,
                total_price: '',
                paid: 0
            },
            new_product: {
                name: '',
                code: '',
            },
            existingIps: [],
            existingIp: '',
            canRefresh: true,
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============

        }
    },
    watch: {
        // ============= NEW_IP START =============
        'new_product': {
            handler(value){
                if(value){
                    this.clearErrors()
                }   
            }, deep: true,
        },
        'new_ip': {
            handler(value){
                if(value){
                    this.clearErrors()
                }   
            }, deep: true,
        },
        // ============= NEW_IP END =============


        



        // ============= OTHER_WATCHERS START =============
        async ipSelect(newValue, oldValue){
            if(newValue || newValue == ''){
                await this.refresh();
            }
        },
        // ============= OTHER_WATCHERS END =============


    },
    async mounted(){
        await this.refresh();
    },
    computed: {
        ...mapState(
            {
                productCodesFields: state => state.code.productCodesFields,
            }
        ),


        // ============= CALCULATE_TOTAL_PRICE START =============
        getTotalPrice() {
            let price = this.new_ip.price.toString().replace(/,/g, '')
            let amount = this.new_ip.amount.toString().replace(/,/g, '');

            let result = price * amount
            
            return  this.new_ip.total_price = new Intl.NumberFormat('en-US').format(result)
        }
        // ============= CALCULATE_TOTAL_PRICE END =============
    },
    methods:{
        ...mapActions(
            {
                saveProductCode: 'code/saveProductCode',
                postNewIp: 'ipAndAksessuar/postNewIp',
                fetchProductCodes: 'code/fetchProductCodes'
            }
        ),
        async addItems(){
            if(this.page < this.last_page){
                this.page = this.page + 1;

                clearTimeout(this._timerId)

                this._timerId = setTimeout(async () => {
                    await this.fetchProductCodes({page: this.page, status: 1, search: this.ipSelect})
                   
                    this.page = this.productCodesFields.current_page;
                    this.pageCount = Math.ceil(this.productCodesFields.total / 20);

                    this.existingIps = this.existingIps.concat(this.productCodesFields.data);
                }, 500)
            }
        },

        scrolledToTop(){

        },












        // ============= REFRESH START =============
        async refresh(){
            clearTimeout(this._timerId)
            
            this.page = 1;

            this._timerId = setTimeout(async () => {
                await this.fetchProductCodes({page: this.page, status: 1, search: this.ipSelect})
                this.page = this.productCodesFields.current_page;
                this.last_page = this.productCodesFields.last_page
                this.pageCount = Math.ceil(this.productCodesFields.total / 20);

                this.existingIps = this.productCodesFields.data
            }, 500)
        },
        // ============= REFRESH END =============










        // ============= PRICE_FORMATTER START =============
        formatPrice(value, input) {
            if(value != ''){

                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);


                if(input == 'amount'){
                    this.new_ip.amount = currency
                }else if(input == 'price'){
                    this.new_ip.price = currency
                }else if(input == 'total_price'){
                    this.new_ip.total_price = currency
                }else if(input == 'paid'){
                    this.new_ip.paid = currency
                }
            }
        },
        // ============= PRICE_FORMATTER END =============










        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.new_ip = {from_where: '', product_id: '', amount: 0,
                price: 0, total_price: '', paid: 0}
            this.new_product = {name: '', code: ''}
            this.date = ''
            this.existingIp = ''
            this.ip_type = 'new'
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeIpAddDialog(){
            this.clearInputs();
            this.$parent.ipAddDialog = false;
            this.$refs.formIpAdd.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============















        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formIpAdd.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============






                    // ============= ADDING_NEW_PRODUCT START =============
                    let product;
                    if(this.ip_type == 'new'){
                        product = await this.saveProductCode({name: this.new_product.name,
                        code: this.new_product.code, product_category_id: 1});
                        
                        if(product.response && product.response.status == 422){
                            if(product.response.data.errors.name){
                                this.errorMessage = product.response.data.errors.name[0];
                                this.errorPlace = 'name'
                            }else if(product.response.data.errors.code){
                                this.errorMessage = product.response.data.errors.code[0];
                                this.errorPlace = 'code'
                            }

                            this.errorStatus = true
                            this.$toast.error(this.errorMessage);
                        }else if(product.response && product.response.status == 409){
                            if(product.response.data.message){
                                this.errorMessage = product.response.data.message;
                            }
                            this.$toast.error(this.errorMessage);
                        }
                    }else if(this.ip_type == 'existing'){
                        product = {id: this.existingIp}
                    }
                    // ============= ADDING_NEW_PRODUCT END =============









                    // ============= ADDING_NEW_IP START =============
                    if(this.errorStatus == false){
                        let ip = await this.postNewIp(
                            { 
                                created_at: this.date,
                                from_where: this.new_ip.from_where,
                                product_id: parseInt(product.id),
                                amount: parseFloat(this.new_ip.amount.toString().replace(/,/g, '')),
                                price: parseFloat(this.new_ip.price.toString().replace(/,/g, '')),
                                total_price: parseFloat(this.new_ip.total_price.toString().replace(/,/g, '')),
                                paid: parseFloat(this.new_ip.paid.toString().replace(/,/g, ''))
                            })
    
                        if(ip.response && ip.response.status == 422){
                            if(ip.response.data.errors.from_where){
                                this.errorMessage = ip.response.data.errors.from_where[0];
                                this.errorPlace = 'from_where'
                            }else if(ip.response.data.errors.amount){
                                this.errorMessage = ip.response.data.errors.amount[0];
                                this.errorPlace = 'amount'
                            }else if(ip.response.data.errors.price){
                                this.errorMessage = ip.response.data.errors.price[0];
                                this.errorPlace = 'price'
                            }else if(ip.response.data.errors.total_price){
                                this.errorMessage = ip.response.data.errors.total_price[0];
                                this.errorPlace = 'total_price'
                            }else if(ip.response.data.errors.paid){
                                this.errorMessage = ip.response.data.errors.paid[0];
                                this.errorPlace = 'paid'
                            }
                            this.errorStatus = true
                            this.$toast.error(this.errorMessage);
                        }else if(ip.response && ip.response.status == 409){
                            if(ip.response.data.message){
                                this.errorMessage = ip.response.data.message;
                            }
                            this.$toast.error(this.errorMessage);
                        }else{
                            let toastTitle = 'Muvaffaqiyatli qo`shildi!'
                            this.$toast.success(toastTitle);
                            this.$store.commit('main/SET_UPDATE_AFTER_IP_ADDED', true)
                            this.closeIpAddDialog();
                            this.refresh()
                            this.$store.commit('main/SET_UPDATE_WAREHOUSE_MATERIAL_INCOME', true)
                            this.$store.commit('main/SET_UPDATE_WAREHOUSE_MATERIAL_EXISTS', true)
                            this.$store.commit('main/SET_UPDATE_MAVJUD_IPS', true)
                        }
                    }
                    // ============= ADDING_NEW_IP END =============







                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        }
        // ============= ON_SUBMIT END =============

    }

}
</script>


<style>

</style>
